import React from 'react';
import { TodoWithUser } from '../../types/TodoWithUser';
import { TodoInfo } from '../TodoInfo';

type Props = {
  todos: TodoWithUser[],
};

export const TodoList: React.FC<Props> = ({ todos }) => (
  <section className="box">
    {todos.map(todo => (
      <TodoInfo
        key={todo.id}
        data-id={todo.id}
        todo={todo}
      />
    ))}
  </section>
);
